.object-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.object-card {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 20px;
  margin: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.object-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.object-card img {
  width: auto;
  height: auto;
}

.object-card .title {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
}

.object-card .description {
  font-size: 14px;
  color: #333;
}
